.nav {
  &__list {
    display: flex;
    padding: 0;
    margin: 0;
    list-style: none;
  }

  &__item {
    margin-left: 20px;

    &:first-child {
      margin-left: 0;
    }
  }

  &__link {
    position: relative;
    display: flex;
    align-items: center;
    height: 60px;
    font-size: 12px;
    line-height: 14px;
    font-weight: 500;
    white-space: nowrap;
    text-decoration: none;
    text-transform: uppercase;
    color: $primary-text-color;
    transition: color 0.3s;

    &:hover {
      color: $primary-design-color;
    }

    &--is--active {
      color: $primary-design-color;
      
      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 4px;
        border-radius: 2px;
        background-color: $primary-design-color;
      }
    }
  }
}
